/* transition */

.transition-all,
a,
.tr,
[class^="btn-"],
[class*=" btn-"],
.wrap-input {
    transition: all .5s ease-in-out;
}


/* transition end */


/* clearfix */

.clearfix,
.buttons,
.bxslider,
.cfx,
[class*="-columns"],
form {
    &:before,
    &:after {
        content: "";
        display: table;
    }
    &:after {
        clear: both;
    }
}


/* clearfix end */





/* reset-list end */


/* inline block */

.inline-b,
[class^="btn-"],
[class*=" btn-"],
[class^="icon-"],
[class*=" icon-"] {
    vertical-align: top;
    display: -moz-inline-stack;
    display: inline-block;
    position: relative;
}


/* inline block end */

/* clear-list */

.clear-list {
    & li:before {
        display: none;
    }
}

/* clear-list end */


/* columns */

.columns {
    @extend .clearfix;
}

.col-50 {
    width: 50%;
    float: left;
}

.col-33 {
    width: 33.33%;
    float: left;
}

.col-20 {
    width: 20%;
    float: left;
}

.col-25 {
    float: left;
    width: 25%;
}


/* columns end */


/* inline-block */


.inline-b {
    display: -moz-inline-stack;
    display: inline-block;
    vertical-align: top;
}

.inline-m {
    display: -moz-inline-stack;
    display: inline-block;
    vertical-align: middle;
}

.inline-bt {
    display: -moz-inline-stack;
    display: inline-block;
    vertical-align: bottom;
}


/* inline-block end */

.bold {
    font-weight: bold;
}

.medium {
    font-weight: 500;
}

.medium-b {
    font-weight: 600;
}

.light {
    font-weight: lighter;
}

.block {
    display: block;
}


.pull-left {
    float: left;
}

.pull-right {
    float: right;
}


/* other reusable styles */

.nowrap {
    white-space: nowrap;
}

.side {
  position: relative;
  &.left,
  &.right {
    position: absolute;
    top: 0;
    bottom: 0;
    @extend .tr;
    width: 280px;
    z-index: 100;
    //height: 100vh;


    .side-inner {
      padding: 15px 15px;
      height: 100%;
      overflow: scroll;
      position: fixed;
      top: 0;
      bottom: 0;
      width: 280px;
    }
  }
  &.left {

    //transform: translateX(-100%);
    left: -100%;
    .side-inner {
      background: #29292b;
    }
    &.visible {
      //transform: translateX(0);
      left: 0px;
    }
  }
  &.right {
    //transform: translateX(1000%);
    background: darken(grey, 20%);
    right: -100%;
    &.visible {
      right: 0;
      //transform: translateX(300%);
    }
  }
}



.user-menu {
  &_header {
    ul {
      @extend .clearfix;
      margin: 0 0 15px;
      li {
        margin: 0;
        padding: 0;
        &:first-child {
          float: left;
        }
        &:last-child {
          float: right;
        }
      }
    }
    span {
      font-size: 0;
      &:before {
        display: block;
        font-size: 20px;

      }
    }
  }
  &_content {
    .user-photo {
      width: 150px;
      height: 150px;
      border-radius: 50px;
      border-radius: 50%;
      border: 1px solid grey;
      margin: 0 auto 25px;
      position: relative;
    }
    .photo-wrap {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      height: 150px;
      width: 150px;
      border-radius: 50%;
      &_inner {
        width: 100%;
        height: 100%;
        border-radius: 50%;
        img {
          border-radius: 50%;
          display: block;
          width: 100%;
          height: 100%;
        }
      }
    }
  }
  &_list {

    padding-top: 0px;
    margin: 0 -15px;
    li {
      display: block;
      padding: 0;
      margin: 0;
      border-top: 1px solid #3e3e41;
      &:last-child {
        border-top: 1px solid #8c88ff;
        text-align: center;
      }
      &:hover,
      &.active {
        a {
          background: grey
        }
      }
      a {
        padding: 15px 15px;
        display: block;
      }
      &:before {
        content: none;
      }
    }
  }
}

.top-menu-holder {
  float: right;
  position: relative;
}


.top-user-image {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  border: 3px solid #8c88ff;
  z-index: 100;
  position: relative;
  cursor: pointer;
  &:before,
  &:after {

  }

  img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    display: block;
  }
}


.top-user-menu {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: 1px solid darken(#fff, 15%);
  background: #66656c;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 99;
  transition:all .5s ease-in-out;
  &.visible {
    width: 250px;
    height: 250px;
    .user-list {
      li {
        opacity: 1;
        visibility: visible;
        &:first-child {
          transform: translate(-100px, -50px);
        }
        &:nth-child(2) {
          transform: translate(-110px, -3px);
        }
        &:nth-child(3) {
          transform: translate(-90px, 40px);
        }

        &:nth-child(4) {
          transform: translate(-55px, 70px);
        }
        &:nth-child(5) {
          transform: translate(-10px, 80px);
        }
      }
    }
  }
}